import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { getPayments } from "../../actions/payment";
import { setAlert } from "../../actions/alert";
import { changePassword } from "../../actions/auth";
import moment from "moment";

const Account = ({ user, loading, getPayments, setAlert, changePassword }) => {
  const [formData, setFormData] = useState({
    email: undefined,
    password: "",
    password2: ""
  });

  const { email, password, password2 } = formData;

  useEffect(() => {
    getPayments();
  }, []);

  const onChange = e =>
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });

  const onSubmit = e => {
    e.preventDefault();
    if (password !== password2) {
      setAlert("Passwords do not match", "error");
    } else {
      changePassword({
        password
      });
    }
  };
  if (!loading) {
    return (
      <div className="flex flex-wrap justify-between -m-4">
        <div className="flex w-full lg:w-1/2 justify-center p-4">
          <div className="w-full">
            <form
              className="bg-red-500 dark:bg-gray-900 shadow-md rounded h-full px-8 pt-6 pb-8 mb-4"
              onSubmit={e => onSubmit(e)}
            >
              <h1 className="text-gray-100 dark:text-gray-200 text-3xl mb-4">
                Account Settings
              </h1>
              <div className="mb-4">
                <label
                  className="block text-gray-200 dark:text-gray-300 text-sm font-bold mb-2"
                  htmlFor="email"
                >
                  Email
                </label>
                <input
                  className="shadow dark:bg-gray-900 dark:text-gray-300 appearance-none border rounded w-full py-2 px-3 bg-red-500 placeholder-gray-200 text-gray-200 leading-tight focus:outline-none focus:shadow-outline"
                  name="email"
                  type="email"
                  autoComplete="no"
                  placeholder="Email"
                  value={user.email}
                  required
                />
              </div>
              <div className="mb-4">
                <label
                  className="block text-gray-200 dark:text-gray-300 text-sm font-bold mb-2"
                  htmlFor="password"
                >
                  New password
                </label>
                <input
                  className="shadow dark:bg-gray-900 appearance-none border rounded w-full py-2 px-3 bg-red-500 placeholder-gray-200 text-gray-200 dark:text-gray-300 leading-tight focus:outline-none focus:shadow-outline"
                  name="password"
                  type="password"
                  autoComplete="no"
                  placeholder="******************"
                  minLength="6"
                  value={password}
                  onChange={e => onChange(e)}
                />
              </div>
              <div className="mb-6">
                <label
                  className="block text-gray-200 dark:text-gray-300 text-sm font-bold mb-2"
                  htmlFor="password"
                >
                  Repeat new password
                </label>
                <input
                  className="shadow dark:bg-gray-900 appearance-none border rounded w-full py-2 px-3 bg-red-500 placeholder-gray-200 text-gray-200 dark:text-gray-300 leading-tight focus:outline-none focus:shadow-outline"
                  name="password2"
                  type="password"
                  autoComplete="no"
                  placeholder="******************"
                  minLength="6"
                  value={password2}
                  onChange={e => onChange(e)}
                />
              </div>
              <div className="flex items-center justify-between">
                <button
                  className="bg-red-500 border hover:bg-red-400 dark:bg-gray-900 dark-hover:bg-gray-800 text-gray-200 dark:text-gray-300 font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
                  type="submit"
                >
                  Save
                </button>
              </div>
            </form>
          </div>
        </div>
        <div className="flex w-full lg:w-1/2 justify-center p-4">
          <div className="w-full">
            <div className="bg-red-500 dark:bg-gray-900 shadow-md h-full rounded px-8 pt-6 pb-8 mb-4">
              <h1 className="text-gray-100 dark:text-gray-200 text-3xl mb-4">
                Payment History
              </h1>
              <div className="w-full mx-auto">
                <div className="bg-white dark:bg-gray-800 shadow-md rounded my-6 h-72 overflow-auto">
                  <table className="text-left text-gray-900 dark:text-gray-200 w-full border-collapse">
                    <thead>
                      <tr>
                        <th className="py-4 px-6 bg-gray-100 dark:bg-gray-700 font-bold uppercase text-sm border-b border-gray-400">
                          Amount
                        </th>
                        <th className="py-4 px-6 bg-gray-100 dark:bg-gray-700 font-bold uppercase text-sm border-b border-gray-400">
                          Date
                        </th>
                        <th className="py-4 px-6 bg-gray-100 dark:bg-gray-700 font-bold uppercase text-sm border-b border-gray-400">
                          Status
                        </th>
                      </tr>
                    </thead>
                    <tbody>
                      {user.payments &&
                        user.payments.map(payment => (
                          <tr className="bg-white dark:bg-gray-800 dark-hover:bg-gray-600 hover:bg-gray-200">
                            <td className="py-4 px-6 border-b border-gray-400">
                              $
                              {
                                payment.paymentDetails.purchase_units[0].amount
                                  .value
                              }
                            </td>
                            <td className="py-4 px-6 border-b border-gray-400">
                              {moment(
                                payment.paymentDetails.create_time
                              ).format("MMMM Do YYYY")}
                            </td>
                            <td className="py-4 px-6 border-b border-gray-400">
                              <span className="rounded text-white bg-green-600 py-1 px-3 text-xs font-bold">
                                {payment.paymentDetails.status}
                              </span>
                            </td>
                          </tr>
                        ))}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="flex w-full lg:w-1/2 justify-center p-4">
          <div className="w-full">
            <div className="bg-red-500 dark:bg-gray-900 shadow-md h-full rounded px-8 pt-6 pb-8 mb-4">
              <h1 className="text-gray-100 dark:text-gray-200 text-3xl mb-4">
                Subscription Details
              </h1>
              <div className="w-full mx-auto">
                <div className="bg-white dark:bg-gray-800 shadow-md rounded my-6 h-72 overflow-auto">
                  <table className="text-left text-gray-900 dark:text-gray-200 w-full border-collapse">
                    <thead>
                      <tr>
                        <th className="py-4 px-6 bg-gray-100 dark:bg-gray-700 font-bold uppercase text-sm border-b border-gray-400">
                          Subscription
                        </th>
                        <th className="py-4 px-6 bg-gray-100 dark:bg-gray-700 font-bold uppercase text-sm border-b border-gray-400">
                          Expiry Date
                        </th>
                        <th className="py-4 px-6 bg-gray-100 dark:bg-gray-700 font-bold uppercase text-sm border-b border-gray-400">
                          Status
                        </th>
                      </tr>
                    </thead>
                    <tbody>
                      {user.roles &&
                        user.roles
                          .filter(role => role.role.rank === 1)
                          .map(role => (
                            <tr className="bg-white dark:bg-gray-800 dark-hover:bg-gray-600 hover:bg-gray-200 hover:bg-gray-200">
                              <td className="py-4 px-6 border-b border-gray-400">
                                Premium
                              </td>
                              <td className="py-4 px-6 border-b border-gray-400">
                                {moment(role.expires_at).format("MMMM Do YYYY")}
                              </td>
                              <td className="py-4 px-6 border-b border-gray-400">
                                {new Date(role.expires_at) > Date.now() ? (
                                  <span className="rounded text-white bg-green-600 py-1 px-3 text-xs font-bold">
                                    ACTIVE
                                  </span>
                                ) : (
                                  <span className="rounded text-white bg-red-400 py-1 px-3 text-xs font-bold">
                                    EXPIRED
                                  </span>
                                )}
                              </td>
                            </tr>
                          ))}
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  } else return null;
};

Account.propTypes = {
  user: PropTypes.object.isRequired,
  loading: PropTypes.bool.isRequired,
  getPayments: PropTypes.func.isRequired,
  setAlert: PropTypes.func.isRequired,
  changePassword: PropTypes.func.isRequired
};

const mapStateToProps = state => ({
  user: state.auth.user,
  loading: state.auth.loading
});

export default connect(mapStateToProps, {
  getPayments,
  setAlert,
  changePassword
})(Account);
