import React, { useState, useEffect } from 'react';
import apiUrl from '../../config/default';
import axios from 'axios';

const Player = ({ network, options }) => {
  const [playerInstance, setPlayerInstance] = useState();

  useEffect(() => {
    const setupPlayer = async () => {
      const {
        slingChannel,
        slingLive,
        espnId,
        adobeRSS,
        startSessionUrl,
        hls_url,
        nhlId,
        mlbId,
      } = options;

      const config = {
        key: 'b03269cd-793c-48c6-83b4-2f080b6737db',
        analytics: {
          key: '5f80b14f-f599-49e4-b610-3ab95c8b6335',
          videoId: 'YOUR VIDEO ID',
          title: 'A descriptive video title',
        },
        remotecontrol: {
          type: 'googlecast',
        },
        network: {
          preprocessHttpRequest: function (type, request) {
            // if (request.url.includes("/keys/")) {
            //   request.headers = {
            //     Authorization:
            //       "eyJraWQiOiI1ODJjNjk1NC0zNDgzLTRhNTktOGQzOC04ZGU3MzUxOTJlMTEiLCJhbGciOiJFZERTQSJ9..As7J5LdsAsYUwY8ichIkZSrrF0wqV_wN1HxAunIPSD-0MyHr0Y4kCTXACNfT0peH0vt_pcKzedZbtp1S1DeHCw",
            //   };
            // }
            if (request.url.includes('https://mf.svc.nhl.com')) {
              request.url = `${apiUrl}/api/premium/nhl/key/${btoa(
                request.url
              )}`;
            }
            if (request.url.includes('https://playback.svcs.mlb.com')) {
              request.url = `https://api.morningstreams.com/api/f1tv/mlbkey/${btoa(
                request.url
              )}`;
            }
          },
        },
      };
      let source;
      if (network === 'Sling' || slingChannel) {
        const state = {};
        const title = 'DubSports | Player';
        const url = `?channel=${slingChannel}`;
        window.history.pushState(state, title, url);
        source = {
          dash: `${apiUrl}/api/premium/sling/channel/${slingChannel}.mpd`,
          drm: {
            widevine: {
              LA_URL: `${apiUrl}/api/premium/sling/key`,
              prepareMessage: function (keyMessage) {
                console.log(keyMessage);
                return JSON.stringify({
                  message: Array.apply(
                    null,
                    new Uint8Array(keyMessage.message)
                  ),
                });
              },
            },
          },
        };
      } else if (network === 'ESPN+') {
        const { data } = await axios.get(
          `${apiUrl}/api/premium/espn/${espnId}`
        );

        source = {
          hls: data,
        };
      } else if (network === 'WatchESPN') {
        const axiosConfig = {
          headers: {
            'Content-Type': 'application/json',
          },
        };
        const body = JSON.stringify({
          resource: encodeURIComponent(adobeRSS),
          startSessionUrl,
        });

        const { data } = await axios.post(
          `${apiUrl}/api/premium/watchespn/play`,
          body,
          axiosConfig
        );

        source = {
          hls: data,
        };
      } else if (network === 'Custom') {
        source = {
          hls: hls_url,
        };
      } else if (network === 'MotoGP') {
        const { data } = await axios.get(`${apiUrl}/api/premium/motogp/play`);

        source = {
          hls: data,
        };
      } else if (network === 'NHL' || nhlId) {
        const state = {};
        const title = 'DubSports | Player';
        const url = `?nhl=${nhlId}`;
        window.history.pushState(state, title, url);

        const { data } = await axios.get(
          `${apiUrl}/api/premium/nhl/play/${nhlId}`
        );

        source = {
          hls: data,
        };
      } else if (network === 'MLB') {
        const state = {};
        const title = 'DubSports | Player';
        const url = `?mlb=${mlbId}`;
        window.history.pushState(state, title, url);

        const { data } = await axios.get(
          `${apiUrl}/api/premium/mlb/play/${mlbId}`
        );

        source = {
          hls: data,
        };
      }
      const player = new window.bitmovin.player.Player(
        document.getElementById('player'),
        config
      );
      setPlayerInstance(player);

      player.load(source).then(
        () => {
          setPlayerInstance(player);
          console.log('Successfully loaded source');
        },
        () => {
          console.log('Error while loading source');
        }
      );
    };
    setupPlayer();
    return () => {
      if (playerInstance) playerInstance.destroy();
    };
  }, []);

  useEffect(() => {
    return () => {
      if (playerInstance) playerInstance.destroy();
    };
  }, [playerInstance]);

  return <div className='absolute inset-0' id='player' />;
};

export default Player;
