import React, { useState } from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { createStream, editStream } from "../../actions/admin";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

const StreamForm = ({
  stream,
  setStream,
  createStream,
  editStream,
  setShowForm,
  editState,
  setEditState
}) => {
  const [formData, setFormData] = useState({
    title: undefined,
    description: undefined,
    category: undefined,
    image_url: undefined,
    sling: undefined,
    sling_channel: undefined,
    hls_url: undefined,
    event_date: undefined,
    active: undefined
  });

  const {
    title,
    description,
    category,
    image_url,
    sling,
    sling_channel,
    hls_url,
    event_date,
    active
  } = formData;

  const onChange = async e => {
    if (e.target.type === "checkbox") {
      setFormData({
        ...formData,
        [e.target.name]: e.target.checked
      });
    } else {
      setFormData({
        ...formData,
        [e.target.name]: e.target.value
      });
    }
  };

  const onSubmit = e => {
    e.preventDefault();
    if (editState) {
      const { _id } = stream;
      console.log(_id);
      editStream({
        _id,
        title,
        description,
        category,
        image_url,
        sling,
        sling_channel,
        hls_url,
        event_date,
        active
      });
    } else {
      createStream({
        title,
        description,
        category,
        image_url,
        sling,
        sling_channel,
        hls_url,
        event_date,
        active
      });
    }
  };

  const onCancel = e => {
    e.preventDefault();
    setFormData({
      title: undefined,
      description: undefined,
      category: undefined,
      image_url: undefined,
      sling: undefined,
      sling_channel: undefined,
      hls_url: undefined,
      event_date: undefined,
      active: undefined
    });
    setStream({});
    setShowForm(false);
    setEditState(false);
  };

  return (
    <form onSubmit={e => onSubmit(e)}>
      <div>
        <div>
          <div>
            <h3 className="text-lg leading-6 font-medium text-gray-900">
              {editState ? "Edit a Stream" : "Create a Stream"}
            </h3>
          </div>
          <div className="mt-6 sm:mt-5">
            <div className="sm:grid sm:grid-cols-3 sm:gap-4 sm:items-start sm:border-t sm:border-gray-200 sm:pt-5">
              <label
                htmlFor="title"
                className="block text-sm font-medium leading-5 text-gray-700 sm:mt-px sm:pt-2"
              >
                Title
              </label>
              <div className="mt-1 sm:mt-0 sm:col-span-2">
                <div className="max-w-xs rounded-md shadow-sm">
                  <input
                    name="title"
                    value={title !== undefined ? title : stream.title}
                    onChange={e => onChange(e)}
                    className="form-input block w-full transition duration-150 ease-in-out sm:text-sm sm:leading-5"
                  />
                </div>
              </div>
            </div>
            <div className="mt-6 sm:mt-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:items-start sm:border-t sm:border-gray-200 sm:pt-5">
              <label
                htmlFor="description"
                className="block text-sm font-medium leading-5 text-gray-700 sm:mt-px sm:pt-2"
              >
                Description
              </label>
              <div className="mt-1 sm:mt-0 sm:col-span-2">
                <div className="max-w-lg flex rounded-md shadow-sm">
                  <textarea
                    name="description"
                    value={
                      description !== undefined
                        ? description
                        : stream.description
                    }
                    onChange={e => onChange(e)}
                    rows="3"
                    className="form-textarea block w-full transition duration-150 ease-in-out sm:text-sm sm:leading-5"
                  ></textarea>
                </div>
                <p className="mt-2 text-sm text-gray-500">Not required.</p>
              </div>
            </div>
            <div className="mt-6 sm:mt-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:items-start sm:border-t sm:border-gray-200 sm:pt-5">
              <label
                htmlFor="category"
                className="block text-sm font-medium leading-5 text-gray-700 sm:mt-px sm:pt-2"
              >
                Category
              </label>
              <div className="mt-1 sm:mt-0 sm:col-span-2">
                <div className="max-w-xs rounded-md shadow-sm">
                  <input
                    name="category"
                    value={category !== undefined ? category : stream.category}
                    onChange={e => onChange(e)}
                    placeholder=""
                    className="form-input block w-full transition duration-150 ease-in-out sm:text-sm sm:leading-5"
                  />
                </div>
              </div>
            </div>
            <div className="mt-6 sm:mt-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:items-start sm:border-t sm:border-gray-200 sm:pt-5">
              <label
                htmlFor="image_url"
                className="block text-sm font-medium leading-5 text-gray-700 sm:mt-px sm:pt-2"
              >
                Image URL
              </label>
              <div className="mt-1 sm:mt-0 sm:col-span-2">
                <div className="max-w-xs rounded-md shadow-sm">
                  <input
                    name="image_url"
                    value={
                      image_url !== undefined ? image_url : stream.image_url
                    }
                    onChange={e => onChange(e)}
                    placeholder=" "
                    className="form-input block w-full transition duration-150 ease-in-out sm:text-sm sm:leading-5"
                  />
                </div>
              </div>
            </div>
            <div className="mt-6 sm:mt-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:items-start sm:border-t sm:border-gray-200 sm:pt-5">
              <label
                htmlFor="image_url"
                className="block text-sm font-medium leading-5 text-gray-700 sm:mt-px sm:pt-2"
              >
                Event Start Date
              </label>
              <DatePicker
                selected={
                  stream.event_date ? new Date(stream.event_date) : event_date
                }
                onChange={date =>
                  setFormData({
                    ...formData,
                    event_date: date
                  })
                }
                showTimeSelect
                timeFormat="HH:mm"
                dateFormat="MMMM d, yyyy HH:mm"
              />
            </div>
            <div className="mt-6 sm:mt-5 sm:border-t sm:border-gray-200 sm:pt-5">
              <fieldset>
                <div className="sm:grid sm:grid-cols-3 sm:gap-4 sm:items-baseline">
                  <div>
                    <legend className="text-base leading-6 font-medium text-gray-900 sm:text-sm sm:leading-5 sm:text-gray-700">
                      Stream Type
                    </legend>
                  </div>
                  <div className="sm:col-span-2">
                    <div className="max-w-lg">
                      <div className="mt-4">
                        <div className="relative flex items-start">
                          <div className="absolute flex items-center h-5">
                            <input
                              name="sling"
                              checked={
                                sling !== undefined ? sling : stream.sling
                              }
                              onChange={e => onChange(e)}
                              type="checkbox"
                              className="form-checkbox h-4 w-4 text-indigo-600 transition duration-150 ease-in-out"
                            />
                          </div>
                          <div className="pl-7 text-sm leading-5">
                            <label
                              htmlFor="sling"
                              className="font-medium text-gray-700"
                            >
                              Sling
                            </label>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </fieldset>
            </div>
            {(sling !== undefined ? (
              sling
            ) : (
              stream.sling
            )) ? (
              <div className="mt-6 sm:mt-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:items-start sm:border-t sm:border-gray-200 sm:pt-5">
                <label
                  htmlFor="sling_channel"
                  className="block text-sm font-medium leading-5 text-gray-700 sm:mt-px sm:pt-2"
                >
                  Sling Channel ID
                </label>
                <div className="mt-1 sm:mt-0 sm:col-span-2">
                  <div className="max-w-xs rounded-md shadow-sm">
                    <input
                      name="sling_channel"
                      value={
                        sling_channel !== undefined
                          ? sling_channel
                          : stream.sling_channel
                      }
                      onChange={e => onChange(e)}
                      placeholder=" "
                      className="form-input block w-full transition duration-150 ease-in-out sm:text-sm sm:leading-5"
                    />
                  </div>
                </div>
              </div>
            ) : (
              <div className="mt-6 sm:mt-5 sm:grid sm:grid-cols-3 sm:gap-4 sm:items-start sm:border-t sm:border-gray-200 sm:pt-5">
                <label
                  htmlFor="hls_url"
                  className="block text-sm font-medium leading-5 text-gray-700 sm:mt-px sm:pt-2"
                >
                  Stream URL
                </label>
                <div className="mt-1 sm:mt-0 sm:col-span-2">
                  <div className="max-w-xs rounded-md shadow-sm">
                    <input
                      name="hls_url"
                      value={hls_url !== undefined ? hls_url : stream.hls_url}
                      onChange={e => onChange(e)}
                      placeholder=" "
                      className="form-input block w-full transition duration-150 ease-in-out sm:text-sm sm:leading-5"
                    />
                  </div>
                </div>
              </div>
            )}
            <div className="mt-6 sm:mt-5 sm:border-t sm:border-gray-200 sm:pt-5">
              <fieldset>
                <div className="sm:grid sm:grid-cols-3 sm:gap-4 sm:items-baseline">
                  <div>
                    <legend className="text-base leading-6 font-medium text-gray-900 sm:text-sm sm:leading-5 sm:text-gray-700">
                      Stream Status
                    </legend>
                  </div>
                  <div className="sm:col-span-2">
                    <div className="max-w-lg">
                      <div className="mt-4">
                        <div className="relative flex items-start">
                          <div className="absolute flex items-center h-5">
                            <input
                              name="active"
                              checked={
                                active !== undefined ? active : stream.active
                              }
                              onChange={e => onChange(e)}
                              type="checkbox"
                              className="form-checkbox h-4 w-4 text-indigo-600 transition duration-150 ease-in-out"
                            />
                          </div>
                          <div className="pl-7 text-sm leading-5">
                            <label
                              htmlFor="active"
                              className="font-medium text-gray-700"
                            >
                              Active
                            </label>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </fieldset>
            </div>
          </div>
        </div>
      </div>
      <div className="mt-8 border-t border-gray-200 pt-5">
        <div className="flex justify-end">
          <span className="inline-flex rounded-md shadow-sm">
            <button
              onClick={e => onCancel(e)}
              type="button"
              className="py-2 px-4 border border-gray-300 rounded-md text-sm leading-5 font-medium text-gray-700 hover:text-gray-500 focus:outline-none focus:border-blue-300 focus:shadow-outline-blue active:bg-gray-50 active:text-gray-800 transition duration-150 ease-in-out"
            >
              Cancel
            </button>
          </span>
          <span className="ml-3 inline-flex rounded-md shadow-sm">
            <button
              type="submit"
              className="inline-flex justify-center py-2 px-4 border border-transparent text-sm leading-5 font-medium rounded-md text-white bg-green-500 hover:bg-green-400 focus:outline-none focus:border-green-700 focus:shadow-outline-green active:bg-green-700 transition duration-150 ease-in-out"
            >
              Submit
            </button>
          </span>
        </div>
      </div>
    </form>
  );
};

StreamForm.propTypes = {
  createStream: PropTypes.func.isRequired,
  editStream: PropTypes.func.isRequired
};

export default connect(null, { createStream, editStream })(StreamForm);
