import React, { useState, useEffect } from "react";
import axios from "axios";
import apiUrl from "../../config/default";
import { Link } from "react-router-dom";
import moment from "moment";
import Modal from "../player/Modal";
import { CSSTransition } from "react-transition-group";

const Channels = (props) => {
  const [channels, setChannels] = useState({
    all: [],
    filtered: [],
  });
  const [modalOpen, setModalOpen] = useState(false);
  const [modalOptions, setModalOptions] = useState();
  const [modalNetwork, setModalNetwork] = useState();
  const [searchString, setSearchString] = useState();
  const [searchCategory, setSearchCategory] = useState();
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const getChannels = async () => {
      const { data } = await axios.get(
        `${apiUrl}/api/premium/sling/channels/${searchCategory || ""}`
      );
      setLoading(false);
      setChannels({ all: data, filtered: data });
    };
    getChannels();
  }, [searchCategory]);

  const onChange = async (e) => {
    setSearchString(e.target.value);
    const searchedChannels = channels.all.filter((channel) =>
      channel.channel_name.toLowerCase().includes(e.target.value.toLowerCase())
    );
    setChannels({ ...channels, filtered: searchedChannels });
  };

  const onSelect = async (e) => {
    setSearchCategory(e.target.value);
  };

  const toggleModal = (modalOptions) => {
    setModalOptions(modalOptions);
    setModalNetwork(modalOptions.network);
    setModalOpen(!modalOpen);
  };

  const convertToHTTPS = (url) => {
    if (url) return url.replace("http:/", "https:/");
  };

  return (
    <>
      <CSSTransition
        in={modalOpen}
        timeout={350}
        classNames="open"
        unmountOnExit
      >
        <Modal
          open={modalOpen}
          setOpen={setModalOpen}
          modalOptions={modalOptions}
          network={modalNetwork}
        />
      </CSSTransition>
      <h1 className="text-3xl text-gray-900 dark:text-gray-300 font-light">
        24/7 Live Channels
      </h1>
      <div className="pt-2">
        <label
          for="searchString"
          className="block text-sm font-medium leading-5 text-gray-900 dark:text-gray-300"
        >
          Search channels
        </label>
        <div className="mt-1 flex rounded-md shadow-sm">
          <div className="relative flex-grow focus-within:z-10">
            <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
              <svg className="h-5 w-5" fill="currentColor" viewBox="0 0 20 20">
                <path
                  fill-rule="evenodd"
                  clip-rule="evenodd"
                  d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z"
                ></path>
              </svg>
            </div>
            <input
              id="searchString"
              value={searchString}
              onChange={(e) => onChange(e)}
              className="form-input block w-full rounded-none rounded-l-md pl-10 transition ease-in-out duration-150 sm:text-sm sm:leading-5"
              placeholder="Channel Name"
            />
            <div class="absolute inset-y-0 right-0 flex items-center">
              <select
                aria-label="Currency"
                className="form-select h-full py-0 pl-2 pr-7 border-transparent bg-transparent text-gray-500 sm:text-sm sm:leading-5"
                value={searchCategory}
                onChange={(e) => onSelect(e)}
              >
                <option value="">All Channels</option>
                <option value="Sports">Sports</option>
                <option value="Entertainment">Entertainment</option>
                <option value="News">News</option>
                <option value="English International">
                  English International
                </option>
                <option value="Kids">Kids</option>
                <option value="Lifestyle">Lifestyle</option>
                <option value="Movies">Movies</option>
                <option value="Premiums">Premiums</option>
              </select>
            </div>
          </div>
        </div>
      </div>
      <div className="flex flex-wrap mt-4 max-h-full -mx-2">
        {channels.filtered.map((channel) => (
          <div className="w-1/2 md:w-1/4 p-2">
            <div
              className="group rounded-lg cursor-pointer overflow-hidden relative h-auto"
              onClick={() =>
                toggleModal({
                  slingChannel: channel.guid,
                  slingLive: true,
                  network: "Sling",
                })
              }
            >
              <div
                className={`h-full w-full absolute bg-black opacity-25 group-hover:opacity-75 transition`}
              ></div>
              <div
                className={`absolute flex items-center justify-center h-full w-full opacity-0 group-hover:opacity-100 transition`}
              >
                <p className="text-white text-center">
                  {channel.title || channel.name}
                  <br />
                  Starts {moment(channel.start_time || channel.utc).fromNow()}
                </p>
              </div>
              <img
                className="h-48 w-auto m-auto object-fit"
                src={convertToHTTPS(
                  (channel.thumbnail && channel.thumbnail.url) ||
                    channel.imageHref
                )}
                alt=""
              />
            </div>
          </div>
        ))}
      </div>
    </>
  );
};

export default Channels;
