import React, { useState, useEffect } from "react";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { completeTransaction } from "../../actions/payment";

const Paypal = ({ completeTransaction, history, duration, cost }) => {
  const { paypal } = window;
  let currentDate = new Date();
  const expires_at = currentDate.setDate(currentDate.getDate() + duration);

  useEffect(() => {
    const renderPayPalButton = () => {
      paypal
        .Buttons({
          createOrder: function(data, actions) {
            // This function sets up the details of the transaction, including the amount and line item details.
            return actions.order.create({
              purchase_units: [
                {
                  amount: {
                    value: cost
                  }
                }
              ]
            });
          },
          onApprove: function(data, actions) {
            // This function captures the funds from the transaction.
            return actions.order.capture().then(function(details) {
              return completeTransaction(data, expires_at, history, cost);
            });
          }
        })
        .render(`#paypal-button-container-${duration}`);
    };
    renderPayPalButton();
  }, []);

  return <div id={`paypal-button-container-${duration}`}></div>;
};

Paypal.propTypes = {
  completeTransaction: PropTypes.func.isRequired
};

export default connect(null, { completeTransaction })(withRouter(Paypal));
