import React, { useState } from "react";
import { connect } from "react-redux";
import { Link, Redirect } from "react-router-dom";
import { setAlert } from "../../actions/alert";
import { register } from "../../actions/auth";
import PropTypes from "prop-types";

const Register = ({ setAlert, register, isAuthenticated }) => {
  const [formData, setFormData] = useState({
    email: "",
    password: "",
    password2: ""
  });

  const { email, password, password2 } = formData;

  const onChange = async e =>
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });

  const onSubmit = e => {
    e.preventDefault();
    if (password !== password2) {
      setAlert("Passwords do not match", "error");
    } else {
      register({
        email,
        password
      });
    }
  };

  if (isAuthenticated) {
    return <Redirect to="/home" />;
  }

  return (
    <div className="flex justify-center">
      <div className="w-full max-w-xl">
        <form
          className="bg-red-500 dark:bg-gray-900 shadow-md rounded px-8 pt-6 pb-8 mb-4"
          onSubmit={e => onSubmit(e)}
        >
          <div className="mb-4">
            <label
              className="block text-gray-200 dark:text-gray-300 text-sm font-bold mb-2"
              htmlFor="email"
            >
              Email
            </label>
            <input
              className="shadow appearance-none bg-red-500 dark:bg-gray-900 dark:text-gray-300 placeholder-gray-200 border rounded w-full py-2 px-3 text-gray-200 leading-tight focus:outline-none focus:shadow-outline"
              name="email"
              type="email"
              placeholder="Email"
              value={email}
              onChange={e => onChange(e)}
              required
            />
          </div>
          <div className="mb-4">
            <label
              className="block text-gray-200 dark:text-gray-300 text-sm font-bold mb-2"
              htmlFor="password"
            >
              Password
            </label>
            <input
              className="shadow appearance-none bg-red-500 dark:bg-gray-900 dark:text-gray-300 placeholder-gray-200 border rounded w-full py-2 px-3 text-gray-200 leading-tight focus:outline-none focus:shadow-outline"
              name="password"
              type="password"
              placeholder="******************"
              minLength="6"
              value={password}
              onChange={e => onChange(e)}
            />
          </div>
          <div className="mb-6">
            <label
              className="block text-gray-200 dark:text-gray-300 text-sm font-bold mb-2"
              htmlFor="password2"
            >
              Repeat password
            </label>
            <input
              className="shadow appearance-none bg-red-500 dark:bg-gray-900 dark:text-gray-300 placeholder-gray-200 border rounded w-full py-2 px-3 text-gray-200 leading-tight focus:outline-none focus:shadow-outline"
              name="password2"
              type="password"
              placeholder="******************"
              minLength="6"
              value={password2}
              onChange={e => onChange(e)}
            />
          </div>
          <div className="flex items-center justify-between">
            <button
              className="bg-red-500 dark:bg-gray-900 dark-hover:bg-gray-800 border hover:bg-red-400 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
              type="submit"
            >
              Register
            </button>
            <Link
              className="inline-block align-baseline font-bold text-sm text-gray-400 hover:text-gray-500"
              to="/login"
            >
              Already have an account? Log in
            </Link>
          </div>
        </form>
      </div>
    </div>
  );
};

Register.propTypes = {
  setAlert: PropTypes.func.isRequired,
  register: PropTypes.func.isRequired,
  isAuthenticated: PropTypes.bool
};

const mapStateToProps = state => ({
  isAuthenticated: state.auth.isAuthenticated
});

export default connect(mapStateToProps, { setAlert, register })(Register);
