import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { getStreams, createStream } from "../../actions/admin";
import SideMenu from "./SideMenu";
import Header from "./Header";
import StreamForm from "./StreamForm";
import StreamTable from "./tables/StreamTable";

const Streams = ({ getStreams, createStream, streams, title }) => {
  const [showForm, setShowForm] = useState(false);
  const [stream, setStream] = useState({});
  const [editState, setEditState] = useState(false);
  const [open, setOpen] = useState(false);

  useEffect(() => {
    if (!streams) getStreams();
  }, []);

  useEffect(() => {
    console.log(stream);
  }, [stream]);

  return (
    <div className="h-screen flex overflow-hidden bg-gray-100">
      <SideMenu open={open} setOpen={setOpen} active={title} />
      <div className="flex flex-col w-0 flex-1 overflow-hidden">
        <Header setOpen={setOpen} />

        <main
          className="flex-1 relative z-0 overflow-y-auto py-6 focus:outline-none"
          tabindex="0"
        >
          <div className="flex justify-between max-w-7xl mx-auto px-4 sm:px-6 md:px-8">
            <h1 className="text-2xl font-semibold text-gray-900">{title}</h1>
            {!showForm && (
              <span class="inline-flex rounded-md shadow-sm">
                <button
                  onClick={() => setShowForm(true)}
                  type="button"
                  class="inline-flex items-center px-4 py-2 border border-transparent text-base leading-6 font-medium rounded-md text-white bg-green-500 hover:bg-green-400 focus:outline-none focus:border-green-700 focus:shadow-outline-green active:bg-green-700 transition ease-in-out duration-150"
                >
                  <svg
                    class="-ml-1 mr-3 h-5 w-5"
                    fill="currentColor"
                    viewBox="0 0 20 20"
                  >
                    <path
                      fill-rule="evenodd"
                      d="M10 3C10.5523 3 11 3.44772 11 4V9H16C16.5523 9 17 9.44772 17 10C17 10.5523 16.5523 11 16 11H11V16C11 16.5523 10.5523 17 10 17C9.44772 17 9 16.5523 9 16V11H4C3.44772 11 3 10.5523 3 10C3 9.44771 3.44772 9 4 9L9 9V4C9 3.44772 9.44772 3 10 3Z"
                      clip-rule="evenodd"
                    />
                  </svg>
                  Add stream
                </button>
              </span>
            )}
          </div>

          {streams ? (
            <div className="max-w-7xl mx-auto px-4 sm:px-6 md:px-8">
              <div className="py-4">
                <div className="flex justify-center">
                  <div className="w-full mx-auto">
                    {showForm && (
                      <StreamForm
                        stream={stream}
                        setStream={setStream}
                        setShowForm={setShowForm}
                        editState={editState}
                        setEditState={setEditState}
                      />
                    )}
                    {!showForm && (
                      <StreamTable
                        setShowForm={setShowForm}
                        setStream={setStream}
                        streams={streams}
                        setEditState={setEditState}
                      />
                    )}
                  </div>
                </div>
              </div>
            </div>
          ) : null}
        </main>
      </div>
    </div>
  );
};

Streams.propTypes = {
  getStreams: PropTypes.func.isRequired,
  createStream: PropTypes.func.isRequired,
  streams: PropTypes.array.isRequired
};

const mapStateToProps = state => ({
  streams: state.admin.streams
});

export default connect(mapStateToProps, { getStreams, createStream })(Streams);
