import React from "react";
import moment from "moment";
import { connect } from "react-redux";
import PropTypes from "prop-types";

import { deleteStream } from "../../../actions/admin";

const StreamTable = ({
  streams,
  deleteStream,
  setShowForm,
  setStream,
  setEditState
}) => {
  const onEdit = (e, stream) => {
    e.preventDefault();
    setShowForm(true);
    setStream(stream);
    setEditState(true);
  };
  return (
    <div className="bg-white dark:bg-gray-800 shadow-md rounded h-72 overflow-auto">
      <table className="text-left text-gray-900 dark:text-gray-200 w-full border-collapse">
        <thead>
          <tr>
            <th className="py-4 px-6 bg-gray-100 dark:bg-gray-700 font-bold uppercase text-sm border-b border-gray-400">
              Title
            </th>
            <th className="py-4 px-6 bg-gray-100 dark:bg-gray-700 font-bold uppercase text-sm border-b border-gray-400">
              Category
            </th>
            <th className="py-4 px-6 bg-gray-100 dark:bg-gray-700 font-bold uppercase text-sm border-b border-gray-400">
              Start date
            </th>
            <th className="py-4 px-6 bg-gray-100 dark:bg-gray-700 font-bold uppercase text-sm border-b border-gray-400">
              Status
            </th>
            <th className="py-4 px-6 bg-gray-100 dark:bg-gray-700 font-bold uppercase text-sm border-b border-gray-400">
              Actions
            </th>
          </tr>
        </thead>
        <tbody>
          {streams.map(stream => (
            <tr className="bg-white dark:bg-gray-800 dark-hover:bg-gray-600 hover:bg-gray-200 hover:bg-gray-200">
              <td className="py-4 px-6 border-b border-gray-400">
                {stream.title}
              </td>
              <td className="py-4 px-6 border-b border-gray-400">
                {stream.category}
              </td>
              <td className="py-4 px-6 border-b border-gray-400">
                {moment(stream.event_date).format("MMMM Do YYYY HH:mm")}
              </td>
              <td className="py-4 px-6 border-b border-gray-400">
                {stream.active ? (
                  <span className="rounded text-white bg-green-600 py-1 px-3 text-xs font-bold">
                    ACTIVE
                  </span>
                ) : (
                  <span className="rounded text-white bg-red-400 py-1 px-3 text-xs font-bold">
                    INACTIVE
                  </span>
                )}
              </td>
              <td className="py-4 px-6 border-b border-gray-400">
                <button
                  type="button"
                  onClick={e => onEdit(e, stream)}
                  className="rounded text-white bg-green-600 py-1 px-3 text-xs font-bold"
                >
                  EDIT
                </button>
                <button
                  type="button"
                  onClick={() => deleteStream(stream._id)}
                  className="rounded text-white bg-red-400 py-1 px-3 ml-2 text-xs font-bold"
                >
                  DELETE
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

StreamTable.propTypes = {
  deleteStream: PropTypes.func.isRequired
};

export default connect(null, { deleteStream })(StreamTable);
