import React, { useEffect, useState } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import './styles.css';

import Navbar from './components/layout/Navbar';
import Login from './components/auth/Login';
import Register from './components/auth/Register';
import Alert from './components/layout/Alert';
import Home from './components/home/Home';
import Landing from './components/home/Landing';
import Footer from './components/layout/Footer';
import Schedule from './components/schedule/Schedule';
import Channels from './components/schedule/Channels';
import Subscribe from './components/premium/Subscribe';
import Account from './components/account/Account';
import Player from './components/player/Player';
import Admin from './components/admin/Admin';
import Users from './components/admin/Users';
import Payments from './components/admin/Payments';
import Streams from './components/admin/Streams';

import PrivateRoute from './components/routing/PrivateRoute';
import PremiumRoute from './components/routing/PremiumRoute';
import AdminRoute from './components/routing/AdminRoute';

import { Provider } from 'react-redux';
import store from './store';

import setAuthToken from './utils/setAuthToken';
import { loadUser } from './actions/auth';

if (localStorage.token) {
  setAuthToken(localStorage.token);
}

const App = ({ location }) => {
  useEffect(() => {
    const setDarkModeClass = () => {
      const htmlEl = document.querySelector('html');
      const darkModeEnabled = JSON.parse(localStorage.getItem('darkMode'));
      if (darkModeEnabled) {
        htmlEl.classList.add('mode-dark');
      }
    };
    setDarkModeClass();
    store.dispatch(loadUser());
  }, []);

  const toggleDarkMode = () => {
    const currentDarkModeSetting = JSON.parse(localStorage.getItem('darkMode'));
    localStorage.setItem('darkMode', !currentDarkModeSetting);
    const htmlEl = document.querySelector('html');
    htmlEl.classList.toggle('mode-dark');
  };

  return (
    <Provider store={store}>
      <Router>
        <div className={`font-sans min-h-screen bg-gray-300 dark:bg-gray-800`}>
          <Navbar toggleDarkMode={toggleDarkMode} />
          <div className='px-8 py-6'>
            <div className='flex justify-center'>
              <Alert />
            </div>
            <Switch>
              <Route exact path='/' component={Landing} />
              <Route exact path='/login' component={Login} />
              <Route exact path='/register' component={Register} />
              <PremiumRoute exact path='/home' component={Home} />
              <PremiumRoute
                exact
                path='/schedule/:category'
                component={Schedule}
              />
              <PremiumRoute
                exact
                path='/channels/:category?'
                component={Channels}
              />
              <PrivateRoute exact path='/subscribe' component={Subscribe} />
              <PrivateRoute exact path='/account' component={Account} />
              <PremiumRoute exact path='/player' component={Player} />
              <div className='-mx-8 -my-6'>
                <AdminRoute
                  exact
                  path='/admin'
                  component={Admin}
                  title='Dashboard'
                />
                <AdminRoute
                  exact
                  path='/admin/users'
                  component={Users}
                  title='Users'
                />
                <AdminRoute
                  exact
                  path='/admin/payments'
                  component={Payments}
                  title='Payments'
                />
                <AdminRoute
                  exact
                  path='/admin/streams'
                  component={Streams}
                  title='Streams'
                />
              </div>
            </Switch>
          </div>
          <Footer />
        </div>
      </Router>
    </Provider>
  );
};

export default App;
