import {
  ADMIN_GET_USERS,
  ADMIN_GET_PAYMENTS,
  ADMIN_GET_ROLES,
  ADMIN_GET_STREAMS,
  ADMIN_CREATE_STREAM,
  ADMIN_DELETE_STREAM,
  ADMIN_EDIT_STREAM
} from "../actions/types";

const initialState = {
  users: null,
  payments: null,
  roles: null,
  streams: null
};

export default function(state = initialState, action) {
  const { type, payload } = action;
  switch (type) {
    case ADMIN_GET_USERS:
      return {
        ...state,
        users: payload
      };
    case ADMIN_GET_PAYMENTS:
      return {
        ...state,
        payments: payload
      };
    case ADMIN_GET_ROLES:
      return {
        ...state,
        roles: payload
      };
    case ADMIN_GET_STREAMS:
      return {
        ...state,
        streams: payload
      };
    case ADMIN_CREATE_STREAM:
      return {
        ...state,
        streams: [...state.streams, action.payload]
      };
    case ADMIN_DELETE_STREAM:
      return {
        ...state,
        streams: state.streams.filter(
          stream => stream._id !== action.payload._id
        )
      };
    case ADMIN_EDIT_STREAM:
      return {
        ...state,
        streams: [
          action.payload,
          ...state.streams.filter(stream => stream._id !== action.payload._id)
        ]
      };
    default:
      return state;
  }
}
