import React, { useState, useEffect } from "react";
import axios from "axios";
import apiUrl from "../../config/default";
import { Link } from "react-router-dom";
import moment from "moment";
import Modal from "../player/Modal";
import { CSSTransition } from "react-transition-group";

const Slider = ({
  category,
  network,
  setNetwork,
  location,
  modalOpen,
  setModalOpen,
  modalOptions,
  setModalOptions,
}) => {
  const [feeds, setFeeds] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const getFeeds = async () => {
      setNetwork(network);
      if (network === "Sling") {
        const { data } = await axios.get(
          `${apiUrl}/api/premium/sling/schedule/${category}`
        );
        setLoading(false);
        setFeeds(data);
      } else if (network === "ESPN+") {
        const { data } = await axios.get(
          `${apiUrl}/api/premium/espn_schedule/${category}`
        );
        setLoading(false);
        setFeeds(data);
      } else if (network === "WatchESPN") {
        const { data } = await axios.get(
          `${apiUrl}/api/premium/watchespn/schedule`
        );
        setLoading(false);
        setFeeds(data);
      } else if (network === "Custom") {
        const { data } = await axios.get(`${apiUrl}/api/premium/streams`);
        setLoading(false);
        setFeeds(data);
      } else if (network === "MotoGP") {
        const { data } = await axios.get(
          `${apiUrl}/api/premium/motogp/schedule`
        );
        setLoading(false);
        setFeeds(data);
      } else if (network === "Channels") {
        const { data } = await axios.get(
          `${apiUrl}/api/premium/sling/channels`
        );
        setLoading(false);
        setFeeds(data);
      } else if (network === "NHL") {
        const currentDate = moment
          .utc()
          .subtract("hours", 6)
          .format("YYYY-MM-DD");
        const { data } = await axios.get(
          `${apiUrl}/api/premium/nhl/schedule/${currentDate}`
        );
        setLoading(false);
        setFeeds(data.dates.length > 0 && data.dates[0].games);
      } else if (network === "MLB") {
        const currentDate = moment
          .utc()
          .subtract("hours", 6)
          .format("YYYY-MM-DD");
        const { data } = await axios.get(
          `${apiUrl}/api/premium/mlb/schedule/${currentDate}`
        );
        setLoading(false);
        setFeeds(data.dates.length > 0 && data.dates[0].games);
      }
    };
    getFeeds();
  }, []);

  const toggleModal = (modalOptions) => {
    setModalOptions(modalOptions);
    setModalOpen(!modalOpen);
  };

  const convertToHTTPS = (url) => {
    if (url) {
      return url.replace("http:/", "https:/");
    } else return null;
  };

  if (!feeds) return null;

  return (
    <>
      <div className="flex flex-wrap mt-2 -mx-2 max-h-full">
        {feeds.slice(0, 5).map((feed, i) => (
          <div key={i} className="w-1/2 sm:w-1/4 md:w-1/5 p-2 flex-grow-0">
            <div
              className={`group rounded-lg ${
                feed.active !== false && "cursor-pointer"
              } overflow-hidden relative h-auto`}
              onClick={() => {
                if (feed.active !== false) {
                  toggleModal({
                    slingChannel:
                      feed.availability_type === "linear"
                        ? feed.channel.guid
                        : feed.external_id || feed.sling_channel,
                    slingLive:
                      feed.availability_type === "linear" ? true : false,
                    espnId: feed.streams && feed.streams[0].id,
                    adobeRSS: feed.adobeRSS,
                    startSessionUrl: feed.airings && feed.airings[0].videoHref,
                    hls_url: feed.hls_url,
                    nhlId:
                      feed.content &&
                      feed.content.media.epg[0].items[0].mediaPlaybackId,
                    mlbId:
                      feed.content &&
                      feed.content.media.epg[0].items[0].mediaId,
                  });
                  setNetwork(network);
                }
              }}
            >
              <div
                className={`h-full w-full absolute bg-black opacity-25 group-hover:opacity-75 transition`}
              ></div>
              <div
                className={`absolute flex items-center justify-center h-full w-full opacity-0 group-hover:opacity-100 transition`}
              >
                <p className="text-white text-center">
                  {category !== "MotoGP"
                    ? feed.title ||
                      feed.name ||
                      `${feed.teams.away.team.name} @ ${feed.teams.home.team.name}`
                    : `${feed.title}: ${feed.category} - ${
                        feed.current_session
                          ? feed.current_session
                          : feed.next_session
                      }`}
                  <br />
                  {feed.start_time ||
                  feed.utc ||
                  feed.event_date ||
                  feed.remain ||
                  feed.gameDate
                    ? feed.is_current_session
                      ? "LIVE"
                      : `Starts ${moment(
                          feed.start_time ||
                            feed.utc ||
                            feed.event_date ||
                            Date.now() + feed.remain * 1000 ||
                            feed.gameDate
                        ).fromNow()}`
                    : null}
                </p>
              </div>
              <img
                className="h-auto object-fit"
                src={convertToHTTPS(
                  category === "NHL"
                    ? "https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/bf401913-999c-418e-88f2-b2a0cd880615/d2ul13n-4273502c-71f0-40f2-b852-94b0528448a7.png?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJzdWIiOiJ1cm46YXBwOiIsImlzcyI6InVybjphcHA6Iiwib2JqIjpbW3sicGF0aCI6IlwvZlwvYmY0MDE5MTMtOTk5Yy00MThlLTg4ZjItYjJhMGNkODgwNjE1XC9kMnVsMTNuLTQyNzM1MDJjLTcxZjAtNDBmMi1iODUyLTk0YjA1Mjg0NDhhNy5wbmcifV1dLCJhdWQiOlsidXJuOnNlcnZpY2U6ZmlsZS5kb3dubG9hZCJdfQ.HuHK3hm7ZAZFjY7a-Tk23KLmcl1zaHcaTcXVShRXs-k"
                    : category === "MLB"
                    ? "https://www.mlbstatic.com/mlb.com/images/share/mlb.jpg?v=1"
                    : (feed.thumbnail && feed.thumbnail.url) ||
                      feed.imageHref ||
                      feed.image_url ||
                      feed.bg_web
                )}
                alt=""
              />
            </div>
          </div>
        ))}
      </div>
    </>
  );
};
export default Slider;
