import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import {
  getUsers,
  getPayments,
  getRoles,
  getStreams
} from "../../actions/admin";
import SideMenu from "./SideMenu";
import Header from "./Header";

const Admin = ({
  getUsers,
  users,
  getPayments,
  payments,
  getRoles,
  roles,
  getStreams,
  streams,
  title
}) => {
  useEffect(() => {
    getUsers();
    getPayments();
    getRoles();
    getStreams();
  }, []);

  const calculateRevenue = () => {
    let arrayToCalc = [];
    const reducer = (accumulator, currentValue) => accumulator + currentValue;

    payments &&
      payments.map(payment => {
        arrayToCalc.push(
          +payment.paymentDetails.purchase_units[0].amount.value
        );
      });
    return arrayToCalc.reduce(reducer);
  };

  const [open, setOpen] = useState(false);

  return (
    <div className="h-screen flex overflow-hidden bg-gray-100">
      <SideMenu open={open} setOpen={setOpen} active={title} />
      <div className="flex flex-col w-0 flex-1 overflow-hidden">
        <Header setOpen={setOpen} />

        <main
          className="flex-1 relative z-0 overflow-y-auto py-6 focus:outline-none"
          tabindex="0"
        >
          <div className="max-w-7xl mx-auto px-4 sm:px-6 md:px-8">
            <h1 className="text-2xl font-semibold text-gray-900">{title}</h1>
          </div>

          {users && payments && roles && streams ? (
            <div className="max-w-7xl mx-auto px-4 sm:px-6 md:px-8">
              <div className="py-4">
                <div className="flex justify-center -mx-2">
                  <div className="flex w-1/4 px-2">
                    <div className="flex justify-between w-full bg-red-500 shadow-xl rounded py-6 px-4">
                      <p className="font-bold text-red-200 tracking-wider text-xs uppercase">
                        Revenue
                        <span className="block text-white text-xl">
                          ${calculateRevenue()}
                        </span>
                      </p>
                    </div>
                  </div>
                  <div className="flex w-1/4 px-2">
                    <div className="flex justify-between w-full bg-red-500 shadow-xl rounded py-6 px-4">
                      <p className="font-bold text-red-200 tracking-wider text-xs uppercase">
                        Payments
                        <span className="block text-white text-xl">
                          {payments.length}
                        </span>
                      </p>
                    </div>
                  </div>
                  <div className="flex w-1/4 px-2">
                    <div className="flex justify-between w-full bg-red-500 shadow-xl rounded py-6 px-4">
                      <p className="font-bold text-red-200 tracking-wider text-xs uppercase">
                        Users
                        <span className="block text-white text-xl">
                          {users.length}
                        </span>
                      </p>
                    </div>
                  </div>
                  <div className="flex w-1/4 px-2">
                    <div className="flex justify-between w-full bg-red-500 shadow-xl rounded py-6 px-4">
                      <p className="font-bold text-red-200 tracking-wider text-xs uppercase">
                        Streams
                        <span className="block text-white text-xl">
                          {streams.length}
                        </span>
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          ) : null}
        </main>
      </div>
    </div>
  );
};

Admin.propTypes = {
  getUsers: PropTypes.func.isRequired,
  users: PropTypes.array.isRequired,
  getPayments: PropTypes.func.isRequired,
  payments: PropTypes.array.isRequired,
  getRoles: PropTypes.func.isRequired,
  roles: PropTypes.array.isRequired,
  getStreams: PropTypes.func.isRequired,
  streams: PropTypes.array.isRequired
};

const mapStateToProps = state => ({
  users: state.admin.users,
  payments: state.admin.payments,
  roles: state.admin.roles,
  streams: state.admin.streams
});

export default connect(mapStateToProps, {
  getUsers,
  getPayments,
  getRoles,
  getStreams
})(Admin);
