import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import Carousel from "./Carousel";
import Slider from "./Slider";
import ChannelSlider from "./ChannelSlider";
import Modal from "../player/Modal";
import { CSSTransition } from "react-transition-group";

const Home = () => {
  const [modalOpen, setModalOpen] = useState(false);
  const [modalOptions, setModalOptions] = useState();
  const [network, setNetwork] = useState("Sling");

  useEffect(() => {
    const autoOpenModal = (channel_id, type) => {
      let modalOptions;
      if (type === "Sling") {
        modalOptions = {
          slingChannel: channel_id,
          slingLive: true,
        };
      } else if (type === "NHL") {
        modalOptions = {
          nhlId: channel_id,
        };
        setNetwork("NHL");
      } else if (type === "MLB") {
        modalOptions = {
          mlbId: channel_id,
        };
        setNetwork("MLB");
      }
      setModalOptions(modalOptions);
      setModalOpen(true);
    };

    let searchParams = new URLSearchParams(window.location.search);
    const channel_id = searchParams.get("channel");
    const nhl_id = searchParams.get("nhl");
    const mlb_id = searchParams.get("mlb");
    if (channel_id) {
      autoOpenModal(channel_id, "Sling");
    }

    if (nhl_id) {
      autoOpenModal(nhl_id, "NHL");
    }

    if (mlb_id) {
      autoOpenModal(mlb_id, "MLB");
    }

    return () => {
      setModalOpen(!modalOpen);
    };
  }, []);

  return (
    <>
      <CSSTransition
        in={modalOpen}
        timeout={350}
        classNames="open"
        unmountOnExit
      >
        <Modal
          open={modalOpen}
          setOpen={setModalOpen}
          modalOptions={modalOptions}
          network={network}
        />
      </CSSTransition>
      <div className="dark:text-gray-400 text-gray-800">
        <div className="flex justify-between">
          <p className="font-semibold">24/7 Live Channels</p>
          <Link
            to={`/channels`}
            className="align-text-bottom whitespace-no-wrap text-gray-700 hover:text-gray-500 dark:text-gray-400 dark-hover:text-gray-300"
          >
            View all
          </Link>
        </div>
        <ChannelSlider category="Channels" network="Channels" />
      </div>
      <div className="pt-8 dark:text-gray-400 text-gray-800">
        <div className="flex justify-between">
          <p className="font-semibold">Upcoming/Live Sports</p>
          <Link
            to={`/schedule/Custom`}
            className="align-text-bottom whitespace-no-wrap text-gray-700 hover:text-gray-500 dark:text-gray-400 dark-hover:text-gray-300"
          >
            View all
          </Link>
        </div>
        <Slider
          category="Upcoming/Live Sports"
          network="Custom"
          setNetwork={setNetwork}
          modalOpen={modalOpen}
          setModalOpen={setModalOpen}
          modalOptions={modalOptions}
          setModalOptions={setModalOptions}
        />
      </div>
      <div className="pt-8 dark:text-gray-400 text-gray-800">
        <p className="font-semibold">Upcoming/Live PPV</p>
        <Slider
          category="Pay Per View"
          network="Sling"
          setNetwork={setNetwork}
          modalOpen={modalOpen}
          setModalOpen={setModalOpen}
          modalOptions={modalOptions}
          setModalOptions={setModalOptions}
        />
      </div>
      <div className="pt-8 dark:text-gray-400 text-gray-800">
        <div className="flex justify-between">
          <p className="font-semibold">Upcoming/Live Soccer</p>
          <Link
            to={`/schedule/Soccer`}
            className="align-text-bottom whitespace-no-wrap text-gray-700 hover:text-gray-500 dark:text-gray-400 dark-hover:text-gray-300"
          >
            View all
          </Link>
        </div>
        <Slider
          category="Soccer"
          network="Sling"
          setNetwork={setNetwork}
          modalOpen={modalOpen}
          setModalOpen={setModalOpen}
          modalOptions={modalOptions}
          setModalOptions={setModalOptions}
        />
      </div>
      <div className="pt-8 dark:text-gray-400 text-gray-800">
        <div className="flex justify-between">
          <p className="font-semibold">Upcoming/Live NBA</p>
          <Link
            to={`/schedule/Pro Basketball`}
            className="align-text-bottom whitespace-no-wrap text-gray-700 hover:text-gray-500 dark:text-gray-400 dark-hover:text-gray-300"
          >
            View all
          </Link>
        </div>
        <Slider
          category="Pro Basketball"
          network="Sling"
          setNetwork={setNetwork}
          modalOpen={modalOpen}
          setModalOpen={setModalOpen}
          modalOptions={modalOptions}
          setModalOptions={setModalOptions}
        />
      </div>
      <div className="pt-8 dark:text-gray-400 text-gray-800">
        <div className="flex justify-between">
          <p className="font-semibold">Upcoming/Live NHL</p>
          <Link
            to={`/schedule/NHL`}
            className="align-text-bottom whitespace-no-wrap text-gray-700 hover:text-gray-500 dark:text-gray-400 dark-hover:text-gray-300"
          >
            View all
          </Link>
        </div>
        <Slider
          category="NHL"
          network="NHL"
          setNetwork={setNetwork}
          modalOpen={modalOpen}
          setModalOpen={setModalOpen}
          modalOptions={modalOptions}
          setModalOptions={setModalOptions}
        />
      </div>
      <div className="pt-8 dark:text-gray-400 text-gray-800">
        <div className="flex justify-between">
          <p className="font-semibold">Upcoming/Live MLB</p>
          <Link
            to={`/schedule/MLB`}
            className="align-text-bottom whitespace-no-wrap text-gray-700 hover:text-gray-500 dark:text-gray-400 dark-hover:text-gray-300"
          >
            View all
          </Link>
        </div>
        <Slider
          category="MLB"
          network="MLB"
          setNetwork={setNetwork}
          modalOpen={modalOpen}
          setModalOpen={setModalOpen}
          modalOptions={modalOptions}
          setModalOptions={setModalOptions}
        />
      </div>
      <div className="pt-8 dark:text-gray-400 text-gray-800">
        <div className="flex justify-between">
          <p className="font-semibold">Upcoming/Live MotoGP</p>
        </div>
        <Slider
          category="MotoGP"
          network="MotoGP"
          setNetwork={setNetwork}
          modalOpen={modalOpen}
          setModalOpen={setModalOpen}
          modalOptions={modalOptions}
          setModalOptions={setModalOptions}
        />
      </div>
      <div className="pt-8 dark:text-gray-400 text-gray-800">
        <div className="flex justify-between">
          <p className="font-semibold">Upcoming/Live UFC (U.S IP only)</p>
          <Link
            to={`/schedule/ESPN+`}
            className="align-text-bottom whitespace-no-wrap text-gray-700 hover:text-gray-500 dark:text-gray-400 dark-hover:text-gray-300"
          >
            View all
          </Link>
        </div>
        <Slider
          category="19826"
          network="ESPN+"
          setNetwork={setNetwork}
          modalOpen={modalOpen}
          setModalOpen={setModalOpen}
          modalOptions={modalOptions}
          setModalOptions={setModalOptions}
        />
      </div>
    </>
  );
};

export default Home;
