import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import moment from "moment";
import { getUsers } from "../../actions/admin";
import SideMenu from "./SideMenu";
import Header from "./Header";

const Users = ({ getUsers, users, title }) => {
  useEffect(() => {
    if (!users) getUsers();
  }, []);

  const findUserRoles = user => {
    const found = user.roles.find(
      role => new Date(role.expires_at) > Date.now()
    );

    if (found) {
      return found.role.name;
    } else {
      return "-";
    }
  };

  const [open, setOpen] = useState(false);

  return (
    <div className="h-screen flex overflow-hidden bg-gray-100">
      <SideMenu open={open} setOpen={setOpen} active={title} />
      <div className="flex flex-col w-0 flex-1 overflow-hidden">
        <Header setOpen={setOpen} />

        <main
          className="flex-1 relative z-0 overflow-y-auto py-6 focus:outline-none"
          tabindex="0"
        >
          <div className="max-w-7xl mx-auto px-4 sm:px-6 md:px-8">
            <h1 className="text-2xl font-semibold text-gray-900">{title}</h1>
          </div>

          {users ? (
            <div className="max-w-7xl mx-auto px-4 sm:px-6 md:px-8">
              <div className="py-4">
                <div className="flex justify-center">
                  <div className="w-full mx-auto">
                    <div className="bg-white dark:bg-gray-800 shadow-md rounded h-72 overflow-auto">
                      <table className="text-left text-gray-900 dark:text-gray-200 w-full border-collapse">
                        <thead>
                          <tr>
                            <th className="py-4 px-6 bg-gray-100 dark:bg-gray-700 font-bold uppercase text-sm border-b border-gray-400">
                              Email
                            </th>
                            <th className="py-4 px-6 bg-gray-100 dark:bg-gray-700 font-bold uppercase text-sm border-b border-gray-400">
                              Active Subscription
                            </th>
                            <th className="py-4 px-6 bg-gray-100 dark:bg-gray-700 font-bold uppercase text-sm border-b border-gray-400">
                              Registration Date
                            </th>
                            <th className="py-4 px-6 bg-gray-100 dark:bg-gray-700 font-bold uppercase text-sm border-b border-gray-400">
                              Actions
                            </th>
                          </tr>
                        </thead>
                        <tbody>
                          {users.map(user => (
                            <tr className="bg-white dark:bg-gray-800 dark-hover:bg-gray-600 hover:bg-gray-200 hover:bg-gray-200">
                              <td className="py-4 px-6 border-b border-gray-400">
                                {user.email}
                              </td>
                              <td className="py-4 px-6 border-b border-gray-400">
                                {findUserRoles(user)}
                              </td>
                              <td className="py-4 px-6 border-b border-gray-400">
                                {moment(user.date).format("MMMM Do YYYY")}
                              </td>
                              <td className="py-4 px-6 border-b border-gray-400">
                                <span className="rounded text-white bg-green-600 py-1 px-3 text-xs font-bold">
                                  EDIT
                                </span>
                                <span className="rounded text-white bg-red-400 py-1 px-3 ml-2 text-xs font-bold">
                                  DELETE
                                </span>
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          ) : null}
        </main>
      </div>
    </div>
  );
};

Users.propTypes = {
  getUsers: PropTypes.func.isRequired,
  users: PropTypes.array.isRequired
};

const mapStateToProps = state => ({
  users: state.admin.users
});

export default connect(mapStateToProps, { getUsers })(Users);
